::-webkit-scrollbar {
    width: 1.6rem;
    &-thumb {
        background-image: linear-gradient(to right bottom, 
        var(--color-primary), var(--color-secondary));
        border-radius: 1rem;
    }
    &-track {
        box-shadow: inset 0 0 .7rem grey;
        border-radius: 1rem;
    }
}